.reactHint:after {
  content: "";
  width: 0;
  height: 0;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 5px solid transparent;
}

.reactHint--top:after {
  top: auto;
  border-bottom: none;
  border-top-color: #000;
}

.reactHint--left:after {
  left: auto;
  border-right: none;
  border-left-color: #000;
}

.reactHint--right:after {
  right: auto;
  border-left: none;
  border-right-color: #000;
}

.reactHint--bottom:after {
  bottom: auto;
  border-top: none;
  border-bottom-color: #000;
}

