#landingPage {
  min-height: 100vh;
  background-color: #209cee;
}

.lazyHero {
  position: relative;
  margin-top: 0vh;
  height: 100vh;
}

.landingHeader {
  position: relative;
  top: -10vh;
}

.jIyzwq {
  opacity: 1 !important;
  height: 100vh;
}

.cfRRNW {
  height: 100vh;
  background-position-y: -550px !important;
}

.bRjMMg {
  position: relative !important;
  min-height: 370px !important;
}

.heroTitle {
  position: relative;
  top: 0;
  /* margin: 0 auto; */
  font-family: "Audiowide", cursive;
  text-shadow: 2px 2px black;
  font-size: 50px !important;
  color: #209cee;
  margin-top: 5% !important;
  margin-bottom: 3%;
  font-weight: 500;
}

.heroText {
  position: relative;
  top: 0;
  font-family: "Audiowide", cursive;
  font-family: "Special Elite", cursive;
  font-size: 32px;
  font-weight: 900;
  color: #209cee;
  padding: 45px;
  width: 80%;
  margin: 0 auto;
  text-shadow: 2px 2px black;
}

.signUpButton {
  position: relative;
  top: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: gold;
  text-shadow: 1px 1px black;
  border: solid 3px #209cee;
  font-size: 1rem;
  font-weight: 900;
}

.loginButton {
  position: relative;
  top: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: gold;
  text-shadow: 1px 1px black;
  border: solid 3px #209cee;
  margin-left: 1%;
  font-size: 1rem;
  font-weight: 900;
}

.emphasis {
  color: gold;
  font-size: 60px;
  font-weight: 600;
  text-shadow: 5px 5px 5px #209cee;
  border-bottom: solid 10px #209cee;
  border-radius: 5%;
  padding-bottom: 2px;
}

.landingBoard {
  margin-top: 10%;
}

.landingBoard h1 {
  color: gold;
}

@media (min-width: 200px) and (max-width: 500px) {
  .heroTitle {
    font-size: 18px !important;
  }
  .heroTitle .emphasis {
    font-size: 20px;
  }
  .heroText {
    font-size: 16px;
  }
  .signUpButton {
    font-size: 14px;
  }
  .loginButton {
    font-size: 14px;
  }
}

@media (min-width: 501px) and (max-width: 800px) {
  .heroTitle {
    font-size: 24px !important;
  }
  .heroTitle .emphasis {
    font-size: 28px;
  }
  .heroText {
    font-size: 20px;
  }
}

@media (min-width: 801px) and (max-width: 1000px) {
  .heroTitle {
    font-size: 40px !important;
  }
  .heroTitle .emphasis {
    font-size: 45px;
  }
  .heroText {
    font-size: 28px;
  }
}

