#adminPage {
  margin-top: 100px;
}

.formAdmin {
  color: gold;
  max-width: 60%;
  margin-left: 20%;
}

.adminFormHeading {
  font-size: 3rem !important;
  margin-bottom: 5vh;
  color: white;
  font-family: "Special Elite", cursive;
}

a {
  color: white;
}

#adminPage {
  font-family: "Special Elite", cursive;
  margin-top: 7%;
  text-align: center;
}

#adminPage h1 {
  color: gold !important;
  margin-bottom: 2%;
}

#adminPage h3 {
  color: white;
}

#chalDeletionPage {
  font-family: "Special Elite", cursive;
  margin-bottom: 10%;
}

.challengeDeletionModal {
  font-family: "Special Elite", cursive;
  color: gold;
}

.deletionRow p {
  font-size: 22px;
  margin: 0 auto;
}

.deletionRow strong {
  color: red;
  font-size: 14px;
  margin: 0 auto;
}

.adminRow {
  justify-content: center;
}

#adminActions {
  margin: 1%;
  text-align: center;
  width: 100%;
}

#adminActions .btn {
  white-space: pre-wrap;
  margin: 2%;
}

.challengeButtons {
  margin: 1%;
}

#adminChals,
#adminUsers {
  margin-top: 10% !important;
}

#editUsersPage {
  color: gold;
  text-align: center;
  margin-top: 10%;
  margin-bottom: 10%;
}

.usersTable {
  text-align: center;
  margin: 0 auto;
}

#adminUsers {
  margin-bottom: 3%;
}

.usersButtons {
  margin: 1%;
}

#challengePage {
  margin-top: 5%;
  margin-bottom: 10%;
  text-align: center;
}

.chalUsers {
  color: gold;
  font-size: 24px;
}

@media (min-width: 200px) and (max-width: 900px) {
  #adminPage {
    margin-top: 25%;
    margin-bottom: 40%;
  }
  #editUsersPage {
    margin-top: 25%;
    margin-bottom: 40%;
  }
  #challengePage {
    margin-top: 25%;
    margin-bottom: 40%;
  }
  #chalDeletionPage {
    margin-top: 25%;
    margin-bottom: 40%;
  }
  #challengePage {
    margin-top: 25%;
    margin-bottom: 40%;
    text-align: center;
  }

  .chalUsers {
    color: gold;
    font-size: 16px;
  }
}

#adminPage {
  margin-bottom: 10%;
}

@media (max-width: 900px) {
  #adminPage {
    margin-top: 30%;
    margin-bottom: 40%;
  }
}
