#homePage {
  max-width: 100vw !important;
  max-height: 100%;
  margin: 0 auto;
  margin-top: 8%;
  font-family: "Audiowide", cursive;
  /* font-family: 'Special Elite', cursive; */
}

.homePageBoard {
  /* margin-top: 8%; */
  max-width: 100vw !important;
  color: white;
  justify-content: center;
  margin-bottom: 10vh;
}

.myChallengesCol,
.messageBoardCol {
  width: 100%;
  margin: 0 auto;
  margin-left: 1vw;
}

.myChallengesCol h1,
.messageBoardCol h1,
.upcomingChallengesCol h1 {
  width: 100%;
  text-align: center;
}

.myChallengesCol,
.upcomingChallengesCol,
.messageBoardCol {
  border-radius: 1rem;
}

/* MY CHALLENGES */
.myChallenges {
  width: 100%;
}
.myChallengesCol {
  text-align: center;
  overflow: auto !important;
  margin-bottom: 5vh;
  padding-bottom: 5vh;
}
.myChallengesCol .challengeButton {
  width: 80%;
  margin: 0 auto;
  display: block;
  margin-top: 1vw;
  margin-bottom: 1vw;
  text-align: center;
}

.myChallengesCol .btn {
  white-space: pre-wrap !important;
  background-color: #209cee;
  border: solid 2px gold;
}

.myChallengesCol,
.upcomingChallengesCol,
.messageBoardCol {
  color: #209cee;
  background-color: rgb(58, 56, 56);
}

.messageBoardCol {
  background-color: rgb(58, 56, 56);
}

/* .upcomingChallengesCol {
    max-width: 700px !important;
} */

.homePageBoard h1 {
  margin-top: 3vh;
  margin-bottom: 3vh;
  font-size: 1.5rem;
  color: white;
}

.upcomingChallengesHr {
  margin: 1vh;
}

/* UPCOMING CHALLENGES */
.upcomingChallenges {
  text-align: center;
  overflow: auto !important;
  min-height: 100vh !important;
  width: 100%;
}
.upcomingChallengesCol {
  text-align: center;
  overflow: auto;
  width: 90%;
  margin: 0 auto;
}
.upcomingChallenges .challengeButton {
  white-space: pre-wrap;
}
.upcomingChallenges .card {
  border: none;
  width: 100%;
}
.upcomingChallenges .challengeCard {
  /* float: left; */
  /* display: inline-block; */
  width: 90%;
  margin: 0 auto;
  margin-bottom: 5%;

  height: auto;
  border: solid 8px gold;
  border-radius: 1%;
}
.challengeCard .titleCol {
  float: left;
  margin: 0 auto;
  background-color: rgb(58, 56, 56);
  padding: 5%;
  border-radius: 4%;
  min-height: 100% !important;
}

.challengeCard .details {
  float: left;
  margin: 0 auto;
}
.challengeCardTitle {
  font-weight: bolder;
  font-size: 16px;
  margin-bottom: 5vh;
}
.challengeCard .card-subtitle {
  font-size: 12px;
  margin: 5px;
}
.challengeCard .card-body {
  color: gold;
  background-color: #209cee;
}

.challengeCard .rulesLink:hover {
  background-color: gold !important;
  color: #209cee !important;
  cursor: pointer !important;
}
.challengeCard .signUpDate {
  color: white !important;
}
.challengeCard .rulesLink {
  position: relative;
  color: gold !important;
  border-radius: 1vw;
  padding: 1rem !important;
  margin: 1vw !important;
}
.challengeCard .signUp {
  border: solid 2px gold;
  font-weight: bolder;
  border-radius: 5%;
  padding: 2%;
  color: lightgreen !important;
  margin-bottom: 1%;
  margin-top: 3vh;
  cursor: pointer;
}
.challengeCard .signUp:hover {
  background-color: gold;
  color: white !important;
}
.challengeInfo {
  font-size: 22px;
  padding: 1vw !important;
  margin-bottom: 1vw;
}
.titleCol .card-title {
  font-size: 24px;
}

/* CHALLENGE MODAL */
.challengeModal {
  align-content: center;
  color: gold;
}

.challengeModal strong {
  margin-left: 1%;
  margin-right: 1%;
  color: lightgreen !important;
}

.thisChallenge,
.challengeStatus {
  margin: 0 auto;
}

.buyIn {
  color: lightgreen !important;
}

.signUpDate,
.remainingSpots {
  color: red !important;
}

/* MESSAGE BOARD */
.messageBoardCol {
  text-align: center;
}

.messageBoard {
  height: auto !important;
  max-width: 90%;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 5% !important;
}

.recentMessagesButton {
  margin: 0 auto;
}

.messageList {
  max-height: 90%;
  min-width: 100%;
  overflow-y: scroll !important;
  text-align: center !important;
}

.messageBox {
  width: 90%;
  position: relative;
  bottom: 0;
  color: white;
  margin: 0 auto;
  margin-top: 10%;
  /* margin-top: 10%; */
  padding-top: 5%;
  border-top: solid 3px gold;
  text-align: center !important;
}

.messageBox label {
  margin: 0 auto !important;
  margin-bottom: 2% !important;
}

#messageType {
  margin-bottom: 1%;
}

.messageBox .submit {
  margin: 0 auto;
  margin-top: 2%;
}

.rce-mbox-text {
  color: black !important;
  font-size: 12px;
  margin: 2%;
  text-align: left !important;
}

.rce-mbox-time {
  font-size: 8px;
}

.challengeCard .card-subtitle {
  font-size: 18px;
}

@media (min-width: 930px) and (max-width: 1100px) {
  .homePageBoard h1 {
    font-size: 20px !important;
  }

  .messageBoard {
    min-width: 100%;
  }

  .messageBoardCol {
    min-width: 100%;
  }

  .challengeCard {
    margin: 0 auto;
    width: 100%;
    max-height: 100% !important;
    height: 300px;
    overflow: auto !important;
  }
  .challengeCard .card-subtitle {
    font-size: 14px;
  }
  .challengeCard .challengeInfo {
    font-size: 12px;
  }
  .challengeCard .card-title {
    font-size: 16px;
  }
  .signUp {
    font-size: 12px !important;
  }
}

@media (min-width: 640px) and (max-width: 929px) {
  /* .homePageBoard {
        margin-top: 14% !important;
    } */
  .homePageBoard h1 {
    font-size: 18px !important;
  }

  .homePageBoard h1 {
    font-size: 20px !important;
  }
  .messageBoard {
    min-width: 100%;
  }

  .messageBoardCol {
    min-width: 100%;
  }
  .challengeCard {
    font-size: 15px;
    margin: 0 auto;
    width: 100%;
    max-height: 100% !important;
    height: 200px;
    overflow: auto !important;
  }

  .challengeCard .card-title {
    font-weight: bolder;
    font-size: 13px !important;
  }
  .challengeCard .card-subtitle {
    font-size: 12px;
    margin: 5px;
  }

  .rce-mbox {
    width: 70%;
  }
  .rce-mbox-text {
    font-size: 15px;
  }

  .rce-mbox-time {
    font-size: 10px;
  }

  .myChallengesCol .challengeButton {
    font-size: 10px;
  }

  .myChallengesCol {
    padding: 0;
  }

  .messageBoardCol {
    margin-top: 8%;
    min-width: 100%;
    text-align: center;
    border-top: solid 2px gold;
  }

  .messageBoardCol h1 {
    padding-top: 5%;
  }

  .messageBox {
    position: relative !important;
    width: 90%;
    text-align: center;
    /* align-content: center; */
    margin-left: 5% !important;
  }

  .messageBoard {
    height: 700px;
    padding: 0;
  }
}

