  }

  .upcomingChallenges {
    min-width: 70%;
    padding-right: 0;
  }

  .challengeCard {
    font-size: 12px;
    margin: 0 auto;
    width: 90% !important;
    /* max-height: 100% !important;  */
    height: 200px;
    overflow: auto !important;
  }

