    font-weight: bolder;
    font-size: 16px !important;
  }
  .challengeCard .card-subtitle {
    font-size: 14px;
    margin: 5px;
  }

  .challengeInfo {
    font-size: 12px !important;
  }
  .rulesLink {
    font-size: 13px;
  }
  .signUp {
    font-size: 13px;
  }
  .rce-mbox {
    width: 70%;
  }
  .rce-mbox-text {
    font-size: 12px;
  }

  .rce-mbox-time {
    font-size: 8px;
  }

  .myChallengesCol .challengeButton {
    font-size: 10px;
  }

  .messageBoardCol {
    margin-top: 8%;
    min-width: 100%;
    text-align: center;
    border-top: solid 2px gold;
  }

  .messageBoardCol h1 {
    padding-top: 5%;
  }

  .messageBox {
    position: relative !important;
    width: 90%;
    text-align: center;
    /* align-content: center; */
    margin-left: 5% !important;
  }

  .messageBoard {
    height: 700px;
    padding: 0;
  }
}

@media (min-width: 300px) and (max-width: 469px) {
  .homePageBoard h1 {
    font-size: 12px !important;
    margin-bottom: 5px;
  }

  .challengeCard {
    font-size: 9px;
    margin: 0 auto;
    width: 100% !important;
    /* max-height: 100% !important;  */
    height: 100%;
    overflow: auto !important;
  }

  .challengeCard .card-title {
    font-weight: bolder;
    font-size: 12px !important;
  }
  .challengeCard .card-subtitle {
    font-size: 12px;
    margin: 5px;
  }

  .challengeInfo {
    font-size: 10px !important;
  }
  .rulesLink {
    font-size: 11px;
  }
  .signUp {
    font-size: 11px;
  }
