  }

  .messageBoardCol {
    margin-top: 8%;
    min-width: 99%;
    text-align: center;
    border-top: solid 2px gold;
    margin-bottom: 20% !important;
  }

  .messageBoardCol h1 {
    padding-top: 5%;
  }

  .messageBox {
    position: relative !important;
    width: 90%;
    text-align: center;
    /* align-content: center; */
    margin-left: 5% !important;
  }

  .messageBoard {
    height: 700px;
    padding: 0;
  }
}

.fa-strikethrough {
  font-size: 14px;
}

@media (max-width: 900px) {
  #homePage {
    margin-top: 20%;
  }
  .fa-strikethrough {
    font-size: 8px;
  }
  .upcomingChallenges {
    max-height: 400px;
    padding-right: 6%;
  }
}

.chalDelay {
  text-align: center;
}
